import React, { useState } from 'react';
import styled from 'styled-components';
import oc from 'open-color';
import { Button, ButtonGroup } from 'reactstrap';
import '../../App.css';



//     ( )   
const Wrapper = styled.div`
   {
        margin-top: 1rem;
    }
`;

const Label = styled.div`
    font-size: 1rem;
    color: ${oc.gray[6]};
    margin-bottom: 0.25rem;
`;



// rest  onChange, type, name, value, placeholder  input      .
const SelectButtonGroup = ({label}) => {
    const [tab, setDealTab] = useState('curr');
    
    return(
    <Wrapper>
        <Label>{label}</Label>

        <Button
            className={`selectBtn ${tab === 'curr' ? 'active' : ''}`}  // tab  'curr'  active  
            onClick={() => setDealTab('curr')} 			//   tab  'curr' . 
            >
            
            </Button>
            <Button
            className={`selectBtn ${tab === 'prev' ? 'active' : ''}`}  // tab  'prev'  active  
            onClick={() => setDealTab('prev')}			 //   tab  'prev' . 
            >
            
        </Button>

       {/* <ButtonGroup>
                <Button
               className={`btn ${tab === 'curr' ? 'active' : ''}`}  // tab  'curr'  active  
               onClick={() => setDealTab('curr')} 			//   tab  'curr' . 
                
                >
                
                </Button>

                
                <Button
                color="primary"
                onClick={function noRefCheck(){}}
                className="button_css"
                >
                
                </Button>   
               
        </ButtonGroup> */}

    </Wrapper>
    );
    
};

export default SelectButtonGroup;